/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import { usePortal } from "Hooks"
import { findPortalElement } from "Utils"
import { useItemsState } from "../../../items/_state/hooks"
import { useProductState } from "../_state/hooks"

function ProductDescription() {
  const itemsState = useItemsState()
  const productState = useProductState()

  const ProductDescriptionCSS = css`
    color: ${itemsState.settings.descriptionColor};

    font-family: ${itemsState.settings.descriptionTypeFontFamily
      ? itemsState.settings.descriptionTypeFontFamily
      : itemsState.settings.descriptionFont
      ? itemsState.settings.descriptionFont
      : "inherit"};

    font-weight: ${itemsState.settings.descriptionTypeFontWeight
      ? itemsState.settings.descriptionTypeFontWeight
      : itemsState.settings.descriptionFontWeight
      ? itemsState.settings.descriptionFontWeight
      : "initial"};

    font-size: ${itemsState.settings.descriptionTypeFontSize
      ? itemsState.settings.descriptionTypeFontSize
      : "initial"};

    font-style: ${itemsState.settings.descriptionTypeFontStyle
      ? itemsState.settings.descriptionTypeFontStyle
      : "initial"};

    letter-spacing: ${itemsState.settings.descriptionTypeLetterSpacing
      ? itemsState.settings.descriptionTypeLetterSpacing
      : "initial"};
    line-height: ${itemsState.settings.descriptionTypeLineHeight
      ? itemsState.settings.descriptionTypeLineHeight
      : "initial"};
    text-decoration: ${itemsState.settings.descriptionTypeTextDecoration
      ? itemsState.settings.descriptionTypeTextDecoration
      : "initial"};
    text-transform: ${itemsState.settings.descriptionTypeTextTransform
      ? itemsState.settings.descriptionTypeTextTransform
      : "initial"};

    margin-bottom: ${itemsState.settings.isSingleComponent ? "0px" : "20px"};

    p:first-of-type {
      margin-top: 0;
    }
  `

  function maybeTruncateDescription() {
    if (!itemsState.settings.descriptionLength) {
      return productState.payload.descriptionHtml
    } else {
      return (
        productState.payload.descriptionHtml.substring(
          0,
          itemsState.settings.descriptionLength
        ) + " ..."
      )
    }
  }

  return usePortal(
    <div
      css={ProductDescriptionCSS}
      className="wps-component-products-description"
      aria-label="Product Description"
      itemProp="description"
      dangerouslySetInnerHTML={{ __html: maybeTruncateDescription() }}
    />,
    findPortalElement(itemsState.settings.dropzoneProductDescription)
  )
}

export default wp.element.memo(ProductDescription)
